.app {
  text-align: center;
}

.headerTitle {
  font-size: 2.5rem;
  margin: 20px 0;
}

.appContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 15px;
  padding: 20px;
  box-sizing: border-box;
}

.visualization {
  border: 1px solid #ccc;
  padding: 20px;
  box-sizing: border-box;
}
